import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Validation from '../../validations/organization'


// Material UI Core //
import Step from '@material-ui/core/Step'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import Stepper from '@material-ui/core/Stepper'
import CloseIcon from '@material-ui/icons/Close'
import StepLabel from '@material-ui/core/StepLabel'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

// Material UI Style //
import { withStyles } from '@material-ui/styles'

const styles = theme => ({
  dialogTitle: {
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  dialogActionsCancel: {
    marginRight: 'auto'
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
    width: '100%',
    '& .MuiInputLabel-outlined.MuiInputLabel-shrink': {
      background: theme.palette.common.white,
      paddingLeft: '5px',
      paddingRight: '5px'
    }
  }
})


class OrganizationDialog extends Component {
  constructor(props) {
    super(props)

    this.state = {
        fieldData: {
          status: 'Active'
        },
        currentStep: 0,
        errors: {}
    }
  }
  
  validateInputs = (btn) => {
    const { fieldData, currentStep } = this.state
    const { isValid, errors } = Validation(fieldData)

    if (isValid) {
      if (btn === 'save') {
        this.props.handleSave(fieldData)
        return
      }
      if (btn === 'next') {
        this.setState({ currentStep: currentStep + 1 })
      }
    } else {
      this.setState({ errors })
    }

    return isValid
  }

  handleInputChanged = (e) => {
    const { id, value } = e.target
    this.setState({
      fieldData: {
        ...this.state.fieldData,
        [id]: value
      }
    })
  }

  handleSelectChanged = (e , id) => {
    const { value } = e.target
    this.setState({
      fieldData: {
        ...this.state.fieldData,
        [id]: value
      }
    })
  }

  handlePrevious = () => {
    const { currentStep } = this.state
    this.setState({ currentStep: currentStep - 1 })
  }


  render() {
    const steps = ['1', '2', '3']
    const { currentStep } = this.state
    const { handleClose, classes } = this.props
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
      PaperProps: {
        style: {
          maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
          width: 250,
        },
      },
    }

    return (
      <Dialog
        open={true}
        fullWidth
        aria-labelledby='simple-dialog-title'
      >
        <MuiDialogTitle disableTypography className={classes.dialogTitle}>
          <Typography variant='h6'>New Organization</Typography>
          <IconButton
            aria-label='close'
            className={classes.closeButton}
            onClick={() => handleClose(false)}
          >
            <CloseIcon />
          </IconButton>
        </MuiDialogTitle>
        <DialogContent dividers>
          <Stepper activeStep={currentStep}>
            {steps.map((label) => {
              return (
                <Step key={label}>
                  <StepLabel></StepLabel>
                </Step>
              )
            })}
          </Stepper>
        </DialogContent>
        <DialogActions>
          <Button 
            color='default'
            variant='outlined'
            onClick={() => handleClose(false)}
            className={classes.dialogActionsCancel} >
            Cancel
          </Button>
          {
            currentStep !== 0
            && (
              <Button onClick={() => this.handlePrevious()} color='primary' variant='contained'>
                Previous
              </Button>
            )
          }
          {
            currentStep !== 2
            && (
              <Button onClick={() => this.validateInputs('next')} color='primary' variant='contained'>
                Next
              </Button>
            )
          }
          {
            currentStep === 2
            && (
              <Button onClick={() => this.validateInputs('save')} color='primary' variant='contained'>
                Save
              </Button>
            )
          }
        </DialogActions>
      </Dialog>
    )
  }
}

OrganizationDialog.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(OrganizationDialog)