import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import _ from 'lodash'
import { organizationSelector } from '../redux/selectors'

// Dialogs //
import OrganizationDialog from '../components/Dialogs/OrganizationDialog'

// Wrappers //
import DetailsWrapper from '../App/hocs/DetailsWrapper'
import ContainerWrapper from '../App/hocs/ContainerWrapper'
import PageTableWrapper from '../App/hocs/PageTableWrapper'

// Material UI Core //
import Icon from '@material-ui/core/Icon'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container'

class Organization extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
			isOpenDialog: false
		}

		this.columns = {
			name: { name: 'Name', sortable: true, defaultActive: true},
			users: { name: 'Users', sortable: true},
			surveys: { name: 'Surveys', sortable: true},
			status: { name: 'Status', sortable: true},
			updated: { name: 'Updated', func: this.formatValue.bind(this, 'updated'), sortable: true},
			actions: {
				name: 'Actions',
				func: this.formatValue.bind(this, 'actions')
			}
		}
	}

	formatValue(key, item) {
		if (key === 'actions') {
			return (
				<Icon
					onClick={() => this.handleView(item)}
				>remove_red_eye</Icon>
			)
		}
		if (key === 'updated') {
			return <span>{item[key]}</span>
		}
	}

	handleView = (item) => {
		this.props.dispatch({
			type: 'GET_ORGANIZATION_DETAILS_REQUESTED',
			payload: item // API: send the entire document for now
		})
	}

	handleNewDialog = (bool) => {
		this.setState({
			isOpenDialog: bool
		})
	}

	handleSortTable = (sorts) => {
		this.props.dispatch({
			type: 'ORGANIZATION_GRID_SORT',
			payload: sorts
		})
	}

	handleSave = (data) => {
		this.setState({
			isOpenDialog: false
		}, () => {
			console.log('dispatching..')
			// dispatch here
			// example below:
			// dispatch(CreateUser(data))
		})
	}

	render() {
    const { isOpenDialog } = this.state
    const { organization: { list: { data } }, activeData } = this.props
    return (
			<Container disableGutters={true} maxWidth={false}>
          {
            activeData.type === 'parent'
            ? (
              <>
								<Button
									color='primary'
									variant='contained'
									onClick={() => this.handleNewDialog(true)}
								>
									New Organization
								</Button>
                <Paper className='MuiPaper-tableWrapper'>
                  <PageTableWrapper
                    rows={data}
                    columns={this.columns}
                    onSortTable={this.handleSortTable}
                  />
                  {
                    isOpenDialog ?
                    <OrganizationDialog 
                      handleSave={this.handleSave}
                      handleClose={this.handleNewDialog}
                    /> : null
                  }
                </Paper>
              </>
            )
            : (
              <DetailsWrapper
                data={activeData}
                entity='user'
              />
            )
          }
        </Container>
    )
  }
}

export default connect(organizationSelector)(ContainerWrapper(Organization))